.Header{display:flex;width:100%;justify-content:space-between;padding:30px 80px;position:fixed;top:0;background-color:#fafafa;z-index:11;box-shadow:0 2px 5px rgba(0,0,0,.1)}.Header__logo{width:45px;height:25px;background-image:url(../../../public/icons/лого-2-вар-1.png);background-size:contain;background-repeat:no-repeat;transition:transform .3s}.Header__logo:hover{transform:scale(1.2)}.Header__list{display:flex;list-style:none}.Header__list :not(:last-child){margin-right:80px}.Header__list__item{display:flex;align-items:center;width:90px}.Header__list__link{text-decoration:none;text-transform:uppercase;color:#212529;position:relative;transition:all .3s}.Header__list__link::after{content:"";position:absolute;display:flex;width:0;height:1.4px;background-color:#212529;border-radius:1%;transition:all .3s}.Header__list__link:hover::after{width:100%}.Header__list__link--active{font-weight:500}.Header__list__link--active::after{content:"";position:absolute;display:flex;width:100%;height:1.4px;background-color:#212529;border-radius:1%;transition:all .3s}.Header__language :not(:last-child){margin-right:20px}.Header__language__link{text-decoration:none;color:gray}.Header__language__link--active{color:#212529}.Header__language__link:hover{color:#212529}@media(max-width: 600px){.Header{padding:30px 40px}.phone-media{display:none}}@media(min-width: 600px)and (max-width: 1180px){.Header{padding:30px 40px}.phone-media{display:none}}